


























































export default {
	data() {
		return {
			// 余额
			balance:0,
			// 遮罩层数据
			maskStatus:false,
			num:false,
			// 提现金额
			money:0,
			name:'',
			cardID:''
		};
	},
	onLoad(option) {
		this.balance = option.balnace
	},
	methods:{
		// 遮罩层确定和取消
		maskClick(e){
			var that = this;
			var id = e.currentTarget.dataset.id;
			console.log(id);
			if(id==1){
				that.maskStatus = !that.maskStatus;
			}else if(id==2){
				that.maskStatus = !that.maskStatus;
			}
		},
		getMoney(e,str){
			if(str == 'money'){
				this.money = e.detail.value
			}else if(str == 'name'){
				this.name = e.detail.value
			}else if(str == 'cardID'){
				this.cardID = e.detail.value
			}
		},
		// 点击到安全密码
		submit(){
			if(this.money==0){
				uni.showToast({
					title:'请输入提现金额!',
					icon:'none'
				})
			}else if(this.name == ''){
				uni.showToast({
					title:'请输入您的姓名或持卡人姓名!',
					icon:'none'
				})
			}else if(this.cardID == ''){
				uni.showToast({
					title:'请输入支付宝账号或银行卡号!',
					icon:'none'
				})
			}else{
				var arr = {
					money:this.money,
					name:this.name,
					cardID:this.cardID,
					way:this.num?'2':'1'
				}
				arr = JSON.stringify(arr);
				uni.navigateTo({
					url:'../pw/pw?arr='+arr
				})
			}
		}
	},
	// 自定义按钮点击事件
	onNavigationBarButtonTap(e){
		if(e.index==0){
			uni.navigateTo({
				url:"../mx/mx"
			})
		}
	},
}
